<style lang="scss" src="./styles/main.scss"></style>
<template>
  <v-ons-navigator
          :page-stack="pageStack"
          @pop-page="storePop"
          :options="options"
  ></v-ons-navigator>
</template>

<script>
  import Loading from './pages/Loading.vue'

  export default {
    beforeCreate () {
      this.$store.commit('navigator/reset', Loading)
    },
    data () {
      return {
        stack: this.$store.state.navigator.stack
      }
    },
    computed: {
      pageStack () {
        // let returnArray = []
        // setTimeout(() => {
          // returnArray = [...this.$store.state.navigator.stack]
        // },);
        console.log('result', this.$store.state.navigator.stack)
        return this.$store.state.navigator.stack
        // return this.stack
        // return []
      },
      options () {
        return this.$store.state.navigator.options
      }
    },
    methods: {
      storePop () {
        this.$store.commit('navigator/pop')
      }
    }
  }
</script>


