<template>
  <v-ons-button class="smallButton" modifier="" @click="push(destination)"> {{ title }}</v-ons-button>
</template>

<script>
  export default {
    name: 'indexHeader',
    props: [
      'title',
      'destination'
    ],
    methods: {
      push (page) {
        this.$store.commit('navigator/push', page)
      }
    }

  }
</script>

<style scoped>

</style>



