<template>
  <v-ons-toolbar>
    <div class="left">
      <v-ons-toolbar-button v-if="showMenu" @click="menuToggle" id="menu-toggle">
        <v-ons-icon icon="ion-navicon, material:md-menu"></v-ons-icon>
      </v-ons-toolbar-button>
      <v-ons-back-button v-if="backLabel"></v-ons-back-button>
      <slot name="left"></slot>
    </div>
    <div class="center"><slot name="center">{{ title }}</slot></div>
    <div class="right"><slot name="right"></slot></div>
  </v-ons-toolbar>
</template>

<script>
  export default {
    props: [
      'title',
      'backLabel',
      'showMenu'
    ],
    methods: {
      menuToggle (e) {
        e.preventDefault()
        $('#wrapper').toggleClass('toggled')
      }
    }
  }
</script>


