const state = {
  offline: true,
  mobileDeviceReady: false,
  keyboardEnterFlush: false
}

// getters
const getters = {
  enterFlushStatus: state => state.keyboardEnterFlush,
  isMobileDeviceReady: state => state.mobileDeviceReady
}

// actions
const actions = {
  CHECK_ONLINE: function ({ commit }, offline) { commit('COMMIT_CHECK_ONLINE', offline) },
  UPDATE_MOBILE_DEVICE_STATUS: function ({ commit }, status) { commit('COMMIT_UPDATE_MOBILE_DEVICE_STATUS', status) },
  UPDATE_ENTER_FLUSH: function ({ commit }, status) { commit('COMMIT_UPDATE_ENTER_FLUSH', status) }
}

// mutations
const mutations = {
  COMMIT_CHECK_ONLINE: (state, offline) => { state.offline = offline },
  COMMIT_UPDATE_MOBILE_DEVICE_STATUS: (state, status) => { state.mobileDeviceReady = status },
  COMMIT_UPDATE_ENTER_FLUSH: (state, status) => { state.keyboardEnterFlush = status }
}

export default {
  strict: true,
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



