// initial state
const state = {
  keyboardArea: null,
  selectCategory: '1'
}

// getters
const getters = {
  getKeyboardArea: (state) => state.keyboardArea,
  getSelectCategory: state => state.selectCategory
}

// actions
const actions = {
  FIND_KEYBOARD_AREA: function ({ commit }, keyboardArea) { commit('COMMIT_KEYBOARD_AREA', keyboardArea) },
  UPDATE_SELECT_CATEGORY: function ({ commit }, category) { commit('COMMIT_UPDATE_SELECT_CATEGORY', category) }
}

// mutations
const mutations = {
  COMMIT_KEYBOARD_AREA: (state, keyboardArea) => { state.keyboardArea = keyboardArea },
  COMMIT_VOID_CART: (state) => { state.selectCategory = '1' },
  COMMIT_UPDATE_SELECT_CATEGORY: (state, selectCategory) => { state.selectCategory = selectCategory }
}

export default {
  strict: true,
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



