<template>
  <v-ons-page id="me">
    <v-header :title="titleName"/>

      <v-ons-list>
        <!--<v-ons-list-item modifier="nodivider" @click="checkHistory()">-->
          <!--<div class="left">购买历史记录</div>-->
          <!--<div class="right">-->
            <!--<v-ons-icon class="right" icon="ion-chevron-right"></v-ons-icon>-->
          <!--</div>-->
        <!--</v-ons-list-item>-->
        <v-ons-list-header>&nbsp;</v-ons-list-header>
        <v-ons-list-item modifier="nodivider" >
          <div class="left">
            Mobile
          </div>
          <div class="center">
            <input class="tel" type="tel" name="SignIn" :value="me.mobile">
          </div>
          <div class="right">
            <v-smallButton class="smallButton1" title="Update" :destination="updateThePhoneNumber"></v-smallButton>
          </div>
        </v-ons-list-item>
        <v-ons-list-header>Account Security</v-ons-list-header>
        <!--<v-ons-list-item modifier="nodivider">-->
          <!--<div class="left">-->
            <!--语言-->
          <!--</div>-->
          <!--<div class="right">-->
            <!--<v-smallButton class="smallButton1" title="更换" :destination="language"></v-smallButton>-->
          <!--</div>-->
        <!--</v-ons-list-item>-->
        <v-ons-list-item modifier="nodivider">
          <div class="left">
            Password
          </div>
          <div class="right">
            <v-smallButton class="smallButton1" title="Update" :destination="changeThePassword"></v-smallButton>
          </div>
        </v-ons-list-item>
        <!--<v-ons-list-item modifier="nodivider">-->
          <!--<div class="left">-->
            <!--指纹-->
          <!--</div>-->
          <!--<div class="right">-->
            <!--<ons-switch checked></ons-switch>-->
          <!--</div>-->
        <!--</v-ons-list-item>-->
        <v-ons-list-header>  </v-ons-list-header>

        <!-- <v-ons-list-item modifier="nodivider">  </v-ons-list-item> -->
      </v-ons-list>
    <!--<v-ons-button @click="reset">Factory reset</v-ons-button>-->
      <v-ons-list-item class="tcdl" modifier="nodivider" @click="logout()">
        <v-bigButton1 title="Logout"></v-bigButton1>
      </v-ons-list-item>
    <v-footer/>
  </v-ons-page>
</template>


<script>
  import Database from '@core/module/Database'
  import smallButton from '../components/button/SmallButton.vue'
  import bigButton1 from '../components/button/BigButton1.vue'
  import changeThePassword from './ChangeThePassword.vue'
  import purchaseHistory from './PurchaseHistory.vue'
  import login from './Login.vue'
  import updateThePhoneNumber from './UpdateThePhoneNumber.vue'

  export default {
    computed: {
      me () {
        return this.$store.getters['account/getAccountData']
      }
    },
    components: {
      'v-smallButton': smallButton,
      'v-bigButton1': bigButton1
    },
    data () {
      return {
        titleName: 'Me',
        changeThePassword: changeThePassword,
        updateThePhoneNumber: updateThePhoneNumber
      }
    },
    methods: {
      checkHistory () {
        this.$store.commit('navigator/push', purchaseHistory)
      },
      reset () {
        let self = this
        this.$ons.notification.confirm({
          message: 'Are you sure want to factory reset?',
          callback: function (answer) {
            if (answer) {
              let db = self.$store.getters['database/getDbInstance']
              let allTables = self.$store.getters['database/allTables']
              let debug = self.$store.getters['env/getDebug']
              Database.resetAllTable(db, allTables, debug, function () {
                self.$ons.notification.alert('Please Exit App and restart it again.')
              })
            }
          }
        })
      },
      logout () {
        let self = this
        this.$store.dispatch('account/RESET').then(() => {
          self.$ons.notification.alert('Logout Success')
          self.$store.commit('navigator/reset', login)
        })
      }
    }
  }
</script>



