<template>
  <v-ons-page id="UpdateThePhoneNumber">
    <v-header :title="titleName"/>
    <section>
      <form action="" class="yh-form yh-form-ftp">
        <v-ons-row>
          <v-ons-col width="10">
            <v-ons-icon class="iphone" icon="ion-iphone"></v-ons-icon>
          </v-ons-col>
          <v-ons-col>
            <input type="number" name="SignIn" placeholder="电话号码">
          </v-ons-col>
        </v-ons-row>
        <v-ons-row>
          <v-ons-col width="10">
            <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
          </v-ons-col>
          <v-ons-col>
            <input type="number" name="SignIn" placeholder="短信验证码">
          </v-ons-col>
          <v-ons-col>
            <v-ons-button class="bigButton bigButton1">
              Send Code
              <!--<span v-if="sendMsgDisabled">({{rTime}} s)</span>-->
            </v-ons-button>
          </v-ons-col>
        </v-ons-row>
      </form>
      <div class="cta_action">
        <div>
          <v-ons-button class="bigButton bigButton1" :destination="me">确认</v-ons-button>
        </div>
      </div>
    </section>

    <v-footer/>
  </v-ons-page>
</template>

<script>
  import me from './Me.vue'
  export default {
    components: {
    },
    data () {
      return {
        titleName: '更新手机号',
        me: me
      }
    }
  }
</script>



