// initial state
const state = {
  currentEnv: null,
  currentDebug: false,
  envList: {
    'localTest': {
      serverUrl: 'http://poscloud.test',
      enableDebug: true,
      autoUpdate: true,
      serverInfo: 'dev'
    },
    'development': {
      serverUrl: 'http://demo.iposcloud.com.au',
      enableDebug: true,
      autoUpdate: true,
      serverInfo: 'dev'
    },
    'production': {
      // serverUrl: 'http://teaser.ibizplus.co',
      serverUrl: 'http://demo.iposcloud.com.au',
      enableDebug: false,
      autoUpdate: true,
      serverInfo: 'prod'
    }
  }
}

// getters
const getters = {
  getDebug: (state) => state.currentDebug,
  getEnv: (state) => state.currentEnv,
  getEnvList: (state) => state.envList
}

// actions
const actions = {
  SET_ENV ({ commit, rootState }, env) {
    let systemEnv = rootState.env.envList[env]
    let debug = systemEnv.enableDebug
    if (debug) {
      console.log('[CORE][Env] SET_ENV start')
      console.log('[CORE][Env] env is:', env)
    }
    commit('COMMIT_SET_ENV', env)
    commit('COMMIT_SET_DEBUG', debug)
    if (debug) {
      console.log('[CORE][Env] SET_ENV finish')
    }
  }
}

// mutations
const mutations = {
  COMMIT_SET_ENV: (state, env) => { state.currentEnv = env },
  COMMIT_SET_DEBUG: (state, currentDebug) => { state.currentDebug = currentDebug }
}

export default {
  strict: true,
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



