<template>
  <v-ons-bottom-toolbar>
    <v-ons-row class="yh-footer">
      <v-ons-col v-for="(item,k) of Menus" :key="k" @click="goTo(item.go,item.id)" :class="isCategoryActive(item.id)">
        <v-ons-icon :class=item.class :icon=item.icon @click="updateCategory(item.id)"></v-ons-icon>
        <span> {{ item.name }} </span>
      </v-ons-col>
      <!--<v-ons-col @click="goTo('homePage')">-->
      <!--<v-ons-icon icon="ion-ios-home-outline"></v-ons-icon><br />-->
      <!--<span> {{ item.name }} </span>-->
      <!--</v-ons-col>-->
      <!--<v-ons-col  width="15" @click="goTo('coupon')">-->
      <!--<v-ons-icon icon="ion-social-hackernews-outline"></v-ons-icon>-->
      <!--<span></span>-->
      <!--</v-ons-col>-->
      <!--<v-ons-col class="main" @click="goTo('menu')">-->
      <!--<div>-->
      <!--<img class="menu_icon" src="../../assets/tea.svg"/>-->
      <!--</div>-->
      <!--</v-ons-col>-->
      <!--<v-ons-col @click="goTo('address')">-->
      <!--<v-ons-icon icon="ion-ios-location-outline"></v-ons-icon>-->
      <!--<span></span>-->
      <!--</v-ons-col>-->
      <!--<v-ons-col @click="goTo('me')">-->
      <!--<v-ons-icon icon="ion-ios-person-outline"></v-ons-icon>-->
      <!--<span></span>-->
      <!--</v-ons-col>-->
    </v-ons-row>
  </v-ons-bottom-toolbar>
</template>

<script>
  import homePage from '../../pages/HomePage.vue'
  import menu from '../../pages/Menu.vue'
  import coupon from '../../pages/Coupon.vue'
  import address from '../../pages/Address.vue'
  import me from '../../pages/Me.vue'
  import changePassword from '../../pages/ChangeThePassword.vue'

  export default {
    props: ['title'],
    methods: {
      created () {
        this.$nextTick(() => {
          this.updateCategory()
        })
      },
      updateCategory (id) {
        if (id !== '1') {
          this.$store.dispatch('system/UPDATE_SELECT_CATEGORY', id)
        } else {
          this.$store.dispatch('system/UPDATE_SELECT_CATEGORY', '1')
        }
      },
      isCategoryActive (categoryId) {
        let isActive = ''
        let currentCategory = this.$store.getters['system/getSelectCategory']
        // console.log(currentCategory)
        // console.log(categoryId)
        if (currentCategory === categoryId) {
          isActive = 'active'
        }
        return isActive
      },
      goTo (page, id) {
        this.updateCategory(id)
        switch (page) {
          case 'homePage':
            this.$store.commit('navigator/reset', homePage)
            break
          case 'menu':
            this.$store.commit('navigator/reset', menu)
            break
          case 'coupon':
            this.$store.commit('navigator/reset', coupon)
            break
          case 'address':
            this.$store.commit('navigator/reset', address)
            break
          case 'me':
            this.$store.commit('navigator/reset', me)
            break
          case 'changePassword':
            this.$store.commit('navigator/reset', changePassword)
            break
        }
      }
    },
    data () {
      return {
        Menus: [
          { id: '1', go: 'homePage', icon: 'ion-ios-home-outline', name: 'Home', class: '' },
          // { go: 'coupon', icon: 'ion-ios-home-outline', name: '优惠卷' },
          { id: '2', go: 'menu', icon: 'ion-ios-book-outline', name: 'Menu' },
          { id: '3', go: 'address', icon: 'ion-ios-location-outline', name: 'Store', class: '' },
          { id: '4', go: 'me', icon: 'ion-ios-person-outline', name: 'Me', class: 'person' }
        ]
      }
    }
  }
</script>



