// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'onsenui/css/onsenui.css'
import 'onsenui/css/onsen-css-components.css'
import '../vendor/bootstrap-sass/assets/javascripts/bootstrap.min.js'
// import '@mobiscroll/jquery/dist/css/mobiscroll.min.css'
import Boostrap from './core/Boostrap'

import Vue from 'vue'
import VueOnsen from 'vue-onsenui'
import store from './store'
import cordova from './core/module/Cordova'
// import mobiscroll from '@mobiscroll/jquery'

import CustomToolbar from './partials/CustomToolbar.vue'
import header from './components/navigation/Header.vue'
import footer from './components/navigation/Footer.vue'
import loading from './pages/Loading.vue'
import AppNavigator from './AppNavigator.vue'

Vue.config.productionTip = false

if (process.env.NODE_ENV !== 'production') {
  Vue.config.devtools = true
}

Vue.use(VueOnsen)

Vue.component('loading', loading)
Vue.component('vHeader', header)
Vue.component('vFooter', footer)

Vue.component('custom-toolbar', CustomToolbar)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(AppNavigator),
  store,
  beforeCreate () {
    cordova.initialize(this.$ons, this.$store)
    // Shortcut for Material Design
    Vue.prototype.md = this.$ons.platform.isAndroid()
    // Set iPhoneX flag based on URL
    if (window.location.search.match(/iphonex/i)) {
      document.documentElement.setAttribute('onsflag-iphonex-portrait', '')
      document.documentElement.setAttribute('onsflag-iphonex-landscape', '')
    }

    // mobiscroll.settings = {
    //   theme: 'ios'
    // }
    // let self = this
    let env = process.env.NODE_ENV
    this.$store.dispatch('env/SET_ENV', env).then(() => {
      Boostrap.init(this.$store, function () {
      })
    })
  }
})



