<template>
  <v-ons-button class="bigButton bigButton2" @click="push(destination)">{{ title }}</v-ons-button>
</template>

<script>
  export default {
    name: 'bigButton2',
    props: [
      'title',
      'destination'
    ],
    methods: {
      push (page) {
        if (page !== undefined) {
          this.$store.commit('navigator/push', page)
        }
      }
    }
  }
</script>


