<template>
  <v-ons-page id="Register">
    <v-header :title="titleName"/>
    <v-logo></v-logo>
    <form action="" class="yh-form yh-form-ze">
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon class="iphone" icon="ion-iphone"></v-ons-icon>
        </v-ons-col>
        <v-ons-col>
          <input type="number" v-model="form.mobile" placeholder="Mobile Number">
        </v-ons-col>
      </v-ons-row>
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
        </v-ons-col>
        <v-ons-col width="50">
          <input type="number" v-model="form.code" placeholder="Verification Code">
        </v-ons-col>
        <v-ons-col>
          <v-ons-button :disabled="disableSendCode" @click="requestCode()">
            Send Code <span v-if="sendMsgDisabled">({{rTime}} s)</span>
          </v-ons-button>
        </v-ons-col>
      </v-ons-row>
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
        </v-ons-col>
        <v-ons-col>
          <input type="password" v-model="form.password" placeholder="Password（6 digital）">
        </v-ons-col>
      </v-ons-row>
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
        </v-ons-col>
        <v-ons-col>
          <input type="password" v-model="form.passwordConfirm"  placeholder="Repeat Password（6 digital）">
        </v-ons-col>
      </v-ons-row>
    </form>
    <section class="register cta_action">
      <v-ons-button :disabled="canSubmit" class="bigButton bigButton1" @click="register()">Sign Up</v-ons-button>
    </section>

    <v-ons-modal :visible="getLoadingVisible">
      <p style="text-align: center">
        Loading <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
        <br><br>
      </p>
    </v-ons-modal>


  </v-ons-page>
</template>

<script>
  import client from '@lib/Rest'
  import mobilePhone from '@lib/MoiblePhone'
  import logo from '../components/navigation/logo.vue'
  import bigButton1 from '../components/button/BigButton1.vue'
  import homePage from './HomePage.vue'

  export default {
    components: {
      'v-bigButton1': bigButton1,
      'v-logo': logo
    },
    computed: {
      canSubmit () {
        let result = true
        if (this.form.mobile && this.form.mobile !== '' && this.form.mobile.length > 5 && this.form.password && this.form.passwordConfirm) {
          result = false
        }
        return result
      },
      getLoadingVisible () {
        return this.$store.getters['loading/getVisible']
      },
      disableSendCode () {
        let result = true
        if (!this.sendMsgDisabled && this.form.mobile && this.form.mobile !== '' && this.form.mobile.length > 5) {
          result = false
        }
        return result
      }
    },
    data () {
      return {
        titleName: 'Sign up',
        rTime: 60, // 发送验证码倒计时
        sendMsgDisabled: false, // 发送验证码按钮状态
        form: {}
      }
    },
    methods: {
      push (page) {
        if (page !== undefined) {
          this.$store.commit('navigator/push', page)
        }
      },
      requestCode () {
        let companyUuid = this.$store.getters['account/getCompanyUuid']
        let requestUrl = this.$store.getters['config/getApiList'].core_sms + '/captcha'
        let apiUrl = this.$store.getters['server/getEndpoint'] + requestUrl
        let that = this

        let data = {
          'mobile': encodeURI(mobilePhone.mobileNumberOutput(this.form.mobile)),
          'company': companyUuid
        }
        let self = this
        client.post(apiUrl, '', data, function (response) {
          if (response.code !== 200) {
            self.$ons.notification.alert(data.message)
          }
        }, function () {
          self.$ons.notification.alert('Code send fail, please try it again later')
        })
        that.sendMsgDisabled = true
        let rTime = that.rTime
        let interval = window.setInterval(() => {
          if (--that.rTime <= 0) {
            that.rTime = rTime
            that.sendMsgDisabled = false
            window.clearInterval(interval)
          }
        }, 1000)
      },
      register () {
        let data = this.form
        let self = this
        if (!data.mobile) {
          this.$ons.notification.alert('Mobile can\'t empty')
          return false
        }
        if (data.password === '') {
          this.$ons.notification.alert('Password can\'t empty')
        } else {
          if (data.password !== data.passwordConfirm) {
            this.$ons.notification.alert('Password not same')
            data.password = ''
            data.passwordConfirm = ''
          } else {
            let companyUuid = this.$store.getters['account/getCompanyUuid']
            let requestUrl = this.$store.getters['config/getApiList'].core_customer + '/register'
            let apiUrl = this.$store.getters['server/getEndpoint'] + requestUrl
            let postData = {
              'mobile': data.mobile,
              'company': companyUuid,
              'password': data.password,
              'captcha': data.code
            }
            this.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', true)
            client.post(apiUrl, '', postData, function (response) {
              self.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', false)
              if (response.code !== 200) {
                self.registerFail(response)
              } else {
                self.registerSuccess(response)
              }
            }, function () {
              self.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', false)
              self.registerFail()
            })
          }
        }
      },
      registerSuccess (response) {
        this.$ons.notification.alert('Register Success!')
        response.data.storeLogin = true
        this.$store.dispatch('account/SAVE_ACCOUNT', response.data).then(() => {
          this.$store.commit('navigator/reset', homePage)
        })
      },
      registerFail (response) {
        let message = 'Register fail'
        if (response !== undefined) {
          message = response.message
        }
        this.form.password = ''
        this.form.passwordConfirm = ''
        this.$ons.notification.alert(message)
      }
    }
  }
</script>



