import Database from '@core/module/Database'
import sync from '@core/module/Sync'

// initial state
const state = {
  all: []
}

// getters
const getters = {
  allCategory: state => state.all
}

// actions
const actions = {
  GET_DATA_FROM_LOCAL ({ commit, rootState }, cb) {
    if (rootState.env.currentDebug) {
      console.log('[CORE][CATEGORY] GET_DATA_FROM_LOCAL start')
    }
    let debug = rootState.env.currentDebug
    Database.getTable(rootState, rootState.database.dbTable.category, function (data) {
      Database.responseUpdate(rootState.database.dbTable.category, data, debug, function () {
        if (rootState.env.currentDebug) {
          console.log('[CORE][CATEGORY] sync to store data is', data.list)
        }
        commit('SYNC_CATEGORY_TO_LOC', data.list)
        if (cb !== undefined) { cb(data) }
      }, function () {
        commit('COMMIT_FAIL_LOGIN')
        if (cb !== undefined) { cb(null) }
      })
    })
    if (debug) {
      console.log('[CORE][CATEGORY][API] GET_DATA_FROM_LOCAL finish')
    }
  },
  GET_DATA_FROM_CLOUD  ({ commit, rootState }, cb) {
    let account = rootState.account.all
    let requestUrl = rootState.config.api['category_api_endpoint'] + '/' + account.company_uuid + '/all'
    sync.getDataFromCloud(rootState, requestUrl, rootState.database.dbTable.category, function (response) {
      commit('SYNC_CATEGORY_TO_LOC', response.data)
    })
  }
}

// mutations
const mutations = {
  SYNC_CATEGORY_TO_LOC (state, data) {
    state.all = data
  }
}

export default {
  strict: true,
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



