import Vue from 'vue'
import Vuex from 'vuex'

// Load core
import database from './store/modules/core/Database'
import config from './store/modules/core/Config'
import server from './store/modules/core/Server'
import system from './store/modules/Sytem'
import account from './store/modules/core/Account'
import env from './store/modules/core/Env'
import queue from './store/modules/core/Queue'
import loading from './store/modules/core/Loading'
// Load basic
import deviceStatus from './store/modules/DeviceStatus'
import product from './store/modules/Product'
import category from './store/modules/Category'
// import Homepage from '@/pages/HomePage'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    navigator: {
      strict: true,
      namespaced: true,
      state: {
        stack: [],
        options: {}
      },
      mutations: {
        push (state, page) {
          state.stack.push(page)
        },
        pop (state) {
          if (state.stack.length > 1) {
            state.stack.pop()
          }
        },
        replace (state, page) {
          state.stack.pop()
          state.stack.push(page)
        },
        reset (state, page) {
          state.stack = [page || state.stack[0]]
        },
        options (state, newOptions = {}) {
          state.options = newOptions
        }
      }
    },
    tabbar: {
      strict: true,
      namespaced: true,
      state: {
        index: 0
      },
      mutations: {
        set (state, index) {
          state.index = index
        }
      }
    },
    splitter: {
      strict: true,
      namespaced: true,
      state: {
        open: false
      },
      mutations: {
        toggle (state, shouldOpen) {
          if (typeof shouldOpen === 'boolean') {
            state.open = shouldOpen
          } else {
            state.open = !state.open
          }
        }
      }
    },
    config,
    env,
    server,
    database,
    system,
    loading,
    account,
    queue,
    deviceStatus,
    product,
    category
  }
})



