<template>
  <div @click="push(destination)">{{ title }}</div>
</template>

<script>
  export default {
    name: 'textButton1',
    props: [
      'title',
      'destination'
    ],
    methods: {
      push (page) {
        this.$store.commit('navigator/push', page)
      }
    }

  }
</script>



