<template>
  <v-ons-page id="ftp">
    <v-header :title="titleName"/>
    <section>
      <v-mobilephonenumberverificationForm></v-mobilephonenumberverificationForm>
      <div class="cta_action">
        <div @click="login()">
         <v-bigButton1 :title="registerName" /><br />
        </div>
      </div>
    </section>

  </v-ons-page>
</template>

<script>
  import bigButton1 from '../components/button/BigButton1.vue'
  import HomePage from './HomePage.vue'
  import MobilePhoneNumberVerification from '../components/form/MobilePhoneNumberVerification.vue'
  export default {
    components: {
      'v-bigButton1': bigButton1,
      'v-mobilephonenumberverificationForm': MobilePhoneNumberVerification
    },
    data () {
      return {
        titleName: '忘记密码',
        registerName: '登录',
        registerPath: HomePage
      }
    },
    methods: {
      login () {
        alert('login')
      }
    }
  }
</script>



