      }
    },
    methods: {
      initScroll () {
        let numer = $('.content > li').length
        if (numer > 0) {
          let liwidth = this.$refs.wrapperli.clientWidth
          let marginR = 15
          let width = (liwidth + marginR) * numer
          this.$refs.wrapperul.style.width = width + 'px'
          // console.log(numer)
          // console.log(width)
          // console.log(liwidth)
          this.shopBoxScroll = new BScroll(this.$refs.wrapper, {
            // better-scroll 会将点击事件去掉，要在这里开启，同时点击在PC 会被执行两次，要在这里控制
            click: true,
            scrollX: true
          })
        } else {
