<template>
  <v-ons-page id="ChangeThePassword">
    <vHeader :title="titleName"/>

    <section>
      <form action="" class="yh-form yh-form-ctp">
        <v-ons-row>
          <v-ons-col width="10">
            <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
          </v-ons-col>
          <v-ons-col>
            <input type="password" name="SignIn" placeholder="输入密码（6位）"></input>
          </v-ons-col>
        </v-ons-row>
        <v-ons-row>
          <v-ons-col width="10">
            <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
          </v-ons-col>
          <v-ons-col>
            <input type="password" name="SignIn" placeholder="输入密码（6位）"></input>
          </v-ons-col>
        </v-ons-row>
        <v-ons-row>
          <v-ons-col width="10">
            <v-ons-icon icon="ion-ios-locked-outline"></v-ons-icon>
          </v-ons-col>
          <v-ons-col>
            <input type="password" name="SignIn" placeholder="输入密码（6位）"></input>
          </v-ons-col>
        </v-ons-row>
      </form>

      <div class="cta_action">
        <div @click="login">
          <v-ons-button class="bigButton bigButton1" :destination="confirmPath">确认</v-ons-button>
        </div>
      </div>
    </section>

    <v-footer />
  </v-ons-page>
</template>

<script>
  import purchaseHistory from '../pages/HomePage.vue'

  export default {
    components: {
    },
    data () {
      return {
        titleName: '更改密码',
        confirmPath: purchaseHistory
      }
    },
    methods: {
      goTo (url) {
        window.open(url, '_blank')
      },
      login () {
         // 强制更改为HOme图标
        this.$store.commit('deviceStatus/COMMIT_UPDATE_SELECT_CATEGORY', '1')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



