<template>
<v-ons-toolbar class="home-toolbar">
    <div class="left">
        <v-ons-back-button></v-ons-back-button>
    </div>
    <div class="center">{{ title }}</div>
</v-ons-toolbar>
</template>

<script>
  import {OnsBackButton} from 'vue-onsenui'
  export default {
    props: [
      'title'
    ],
    components: {
      OnsBackButton
    }
  }
</script>



