<template>
  <v-ons-page id="login">
    <v-logo></v-logo>
    <form action="" class="yh-form yh-form-dl">
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon class="iphone" icon="ion-iphone"></v-ons-icon>
        </v-ons-col>
        <v-ons-col>
          <input type="number" v-model="form.mobile" placeholder="Phone Number">
        </v-ons-col>
      </v-ons-row>
      <v-ons-row>
        <v-ons-col width="10">
          <v-ons-icon  icon="ion-ios-locked-outline"></v-ons-icon>
        </v-ons-col>
        <v-ons-col>
          <input type="password" v-model="form.password" placeholder="Password（6 digital）">
        </v-ons-col>
      </v-ons-row>
    </form>
    <!--<form action="">-->
    <!--<v-ons-list-item>-->
    <!--<div class="left">-->
    <!--<v-ons-icon style="font-size: 30px" icon="ion-iphone"></v-ons-icon>-->
    <!--</div>-->
    <!--<div class="center">-->
    <!--<v-ons-input type="number" placeholder="Phone Number" float-->
    <!--v-model="form.mobile"-->
    <!--&gt;-->
    <!--</v-ons-input>-->
    <!--</div>-->
    <!--</v-ons-list-item>-->
    <!--<v-ons-list-item>-->
    <!--<div class="left">-->
    <!--<v-ons-icon style="font-size: 25px" class="iphone" icon="ion-ios-locked-outline"></v-ons-icon>-->
    <!--</div>-->
    <!--<div class="center">-->
    <!--<v-ons-input type="password" placeholder="Password（6 digital）" float-->
    <!--v-model="form.password"-->
    <!--&gt;-->
    <!--</v-ons-input>-->
    <!--</div>-->
    <!--</v-ons-list-item>-->
    <!--</form>-->

    <section class="signIn cta_action">
      <!--<v-textButton1 class="yh-wjmm" title="Forget Password?" :destination="ForgetThePassword"></v-textButton1><br />-->
      <v-ons-button class="bigButton bigButton1" @click="login()">LOG IN</v-ons-button>
      <div class="cta">
        <v-ons-button class="bigButton bigButton2" @click="signUp()">SIGN UP</v-ons-button>
      </div>
    </section>
    <v-ons-modal :visible="getLoadingVisible">
      <p style="text-align: center">
        Loading <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
        <br><br>
      </p>
    </v-ons-modal>
  </v-ons-page>
</template>
<script>
  import client from '@lib/Rest'
  import logo from '../components/navigation/logo.vue'
  import utli from '../library/Utli'
  import bigButton1 from '../components/button/BigButton1.vue'
  import bigButton2 from '../components/button/BigButton2.vue'
  import text1 from '../components/button/Text1.vue'
  import register from './Register.vue'
  import ForgetThePassword from './ForgetThePassword.vue'
  import homePage from '../pages/HomePage'

  export default {
    mounted () {
      utli.changeBackgroudClass('redBackground')
    },
    components: {
      'v-bigButton1': bigButton1,
      'v-bigButton2': bigButton2,
      'v-textButton1': text1,
      'v-logo': logo
    },
    computed: {
      getLoadingVisible () {
        return this.$store.getters['loading/getVisible']
      }
    },
    data () {
      return {
        register: register,
        form: {},
        ForgetThePassword: ForgetThePassword
      }
    },
    methods: {
      validate () {
        let check = true
        if (!this.form.mobile || this.form.mobile === '') {
          this.$ons.notification.alert('Mobile phone can\'t be empty')
          check = false
        } else {
          if (!this.form.password) {
            this.$ons.notification.alert('Password  can\'t be empty')
            check = false
          } else {
            if (this.form.password && this.form.password.length < 5) {
              this.$ons.notification.alert('Password must be greater then 6 digital')
              check = false
            }
          }
        }
        return check
      },
      login () {
        let self = this
        let result = this.validate()
        if (result) {
          this.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', true)
          let companyUuid = this.$store.getters['account/getCompanyUuid']
          let requestUrl = this.$store.getters['config/getApiList'].core_customer + '/login'
          let apiUrl = this.$store.getters['server/getEndpoint'] + requestUrl
          let postData = {
            'mobile': this.form.mobile,
            'company': companyUuid,
            'password': this.form.password
          }
          client.post(apiUrl, '', postData, function (response) {
            self.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', false)
            if (response.code === 200) {
              self.loginSuccess(response)
            } else {
              self.loginFail()
            }
          }, function () {
            self.$store.dispatch('loading/UPDATE_LOADING_VISIBLE', false)
            self.loginFail()
          })
        }
      },
      loginSuccess (response) {
        let self = this
        response.data.storeLogin = true
        this.$store.dispatch('account/SAVE_ACCOUNT', response.data).then(() => {
          self.$store.commit('navigator/reset', homePage)
          // 强制更改为HOme图标
          self.$store.commit('system/COMMIT_UPDATE_SELECT_CATEGORY', '1')
        })
      },
      loginFail () {
        this.$ons.notification.alert('Login fail, please try it again!')
        this.form.password = ''
      },
      signUp () {
        this.$store.commit('navigator/push', register)
      }
    }
  }
</script>



