<template>
  <v-ons-page id="coupon">
    <v-header :title="titleName"/>
    <!--选项-->
    <div class="tabbar tabbar--top tabbar--material">
      <label @click="selectTypeFn(1)" class="tabbar__item tabbar--material__item">
        <input type="radio" name="tabbar-material-a" checked="checked">
        <button  class="tabbar__button tabbar--material__button">
          未使用
        </button>
      </label>
      <label @click="selectTypeFn(2)" class="tabbar__item tabbar--material__item">
        <input type="radio" name="tabbar-material-a">
        <button class="tabbar__button tabbar--material__button">
          已使用
        </button>
      </label>
      <label @click="selectTypeFn(3)" class="tabbar__item tabbar--material__item">
        <input type="radio" name="tabbar-material-a">
        <button class="tabbar__button tabbar--material__button">
          已过期
        </button>
      </label>
    </div>

    <!--未使用-->
    <div v-show="isExpanded">
      <v-ons-list class="notUsed" @click="checkHistory()">
        <ons-list-item modifier="nodivider">
          <v-ons-row>
            <v-ons-col>
              BUY 1 GET 1
            </v-ons-col>
            <v-ons-col width="40">
              <figure>
                <div>Buy 1 Get 1</div>
                <div>for new customer</div>
              </figure>
            </v-ons-col>
          </v-ons-row>
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Register and get one cup of Milk tea free.
          availableforall shops.
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Valid until 29 6月 2018
        </ons-list-item>
      </v-ons-list>
    </div>

    <!--已使用-->
    <div v-show="isExpandedone">
      <v-ons-list class="notUsed">
        <ons-list-item modifier="nodivider">
          <v-ons-row>
            <v-ons-col>
              BUY 2 GET 2
            </v-ons-col>
            <v-ons-col width="40">
              <figure>
                <div>Buy 1 Get 1</div>
                <div>for new customer</div>
              </figure>
            </v-ons-col>
          </v-ons-row>
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Register and get one cup of Milk tea free.
          availableforall shops.
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Valid until 29 6月 2018
        </ons-list-item>
      </v-ons-list>
    </div>

    <!--已过期-->
    <div v-show="isExpandedtwo">
      <v-ons-list class="notUsed">
        <ons-list-item modifier="nodivider">
          <v-ons-row>
            <v-ons-col>
              BUY 3 GET 3
            </v-ons-col>
            <v-ons-col width="40">
              <figure>
                <div>Buy 1 Get 1</div>
                <div>for new customer</div>
              </figure>
            </v-ons-col>
          </v-ons-row>
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Register and get one cup of Milk tea free.
          availableforall shops.
        </ons-list-item>
        <ons-list-item modifier="nodivider">
          Valid until 29 6月 2018
        </ons-list-item>
      </v-ons-list>
    </div>
    <v-footer/>
  </v-ons-page>
</template>

<script>
  import small1 from '../components/button/SmallButton.vue'
  import validationPage from '../library/Validation'
  import purchaseHistory from './DetailsCoupon.vue'

  export default {
    beforeCreate () {
      validationPage.check(this.$store)
    },
    components: {
      'small1': small1
    },
    data () {
      return {
        titleName: '我的优惠卷',
        registerName: '确认',
        isExpanded: true,
        isExpandedone: false,
        isExpandedtwo: false
      }
    },
    methods: {
      checkHistory () {
        this.$store.commit('navigator/push', purchaseHistory)
      },
      selectTypeFn (type) {
        console.log(type)
        if (type === 1) {
          this.isExpanded = true
          this.isExpandedone = false
          this.isExpandedtwo = false
        }
        if (type === 2) {
          this.isExpanded = false
          this.isExpandedone = true
          this.isExpandedtwo = false
        }
        if (type === 3) {
          this.isExpanded = false
          this.isExpandedone = false
          this.isExpandedtwo = true
        }
      }
    }
  }
</script>



