import Database from '@core/module/Database'
import sync from '@core/module/Sync'
import _ from '../../../vendor/underscore/underscore'

// initial state
const state = {
  all: []
}

// getters
const getters = {
  allProducts: state => state.all
}

// actions
const actions = {
  GET_DATA_FROM_LOCAL ({ commit, rootState }, cb) {
    if (rootState.env.currentDebug) {
      console.log('[CORE][PRODUCT] GET_DATA_FROM_LOCAL start')
    }
    let debug = rootState.env.currentDebug
    Database.getTable(rootState, rootState.database.dbTable.product, function (data) {
      Database.responseUpdate(rootState.database.dbTable.product, data, debug, function () {
        if (rootState.env.currentDebug) {
          console.log('[CORE][PRODUCT] sync to store data is', data.list)
        }
        commit('SYNC_PRODUCT_TO_LOC', data.list)
        if (cb !== undefined) { cb(data) }
      }, function () {
        commit('COMMIT_FAIL_LOGIN')
        if (cb !== undefined) { cb(null) }
      })
    })
    if (debug) {
      console.log('[CORE][PRODUCT][API] GET_DATA_FROM_LOCAL finish')
    }
  },
  GET_DATA_FROM_CLOUD  ({ commit, rootState }, cb) {
    let account = rootState.account.all
    let requestUrl = rootState.config.api['product_api_endpoint'] + '/' + account.company_uuid + '/all'
    sync.getDataFromCloud(rootState, requestUrl, rootState.database.dbTable.product,
        (response) => {
          if (rootState.env.currentDebug) {
            console.log('[CORE][PRODUCT][GET_DATA_FROM_CLOUD] data is ', response.data)
          }
          if (cb !== undefined) cb(response.data)
          commit('SYNC_PRODUCT_TO_LOC', response.data)
        },
        (response) => {
          response.data = _.map(response.data, function (result) {
            result.productAttributeFormat = _.groupBy(result.product_attribute_types, 'required')
            return result
          })
          return response
        })
  }
}

// mutations
const mutations = {
  SYNC_PRODUCT_TO_LOC (state, data) {
    state.all = data
  },
  setProducts (state, products) {
    state.all = products
  },

  decrementProductInventory (state, { id }) {
    const product = state.all.find(product => product.id === id)
    product.inventory--
  }
}

export default {
  strict: true,
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



